<template>
  <div class="add">
  	<h3>标题</h3>
  	<input type="" name="" id="" value="" placeholder="请输入标题" v-model="title"/>
  	<h3>内容</h3>
  	<textarea name="" rows="" cols="" placeholder="请输入内容" v-model="content"></textarea>
  	<div class="add_btn" @click="add()">添加</div>
  	<div class="add_btn cancel" @click="cancel()">取消</div>
  </div>
</template>

<script>
// @ is an alias to /src
import store from '@/store'
export default {
  name: 'add',
  store,
  data(){
  	return {
  		title:'',
  		content:''
  	}
  },
  methods:{
  	add(){
  		store.commit('addItem',{
  			title:this.title,
  			content:this.content
  		});
  		this.title = "";
  		this.content = "";
  		this.$router.push('/home/list');
  	},
  	cancel(){
  		this.title = "";
  		this.content = "";
  		this.$router.push('/home/user');
  	}
  }
};
</script>

<style>
h3{
	font-size: 14px;
}
input{
	border: none;
	border-bottom: 1px solid #42B983;
	margin: 0 25px;
	outline: none;
	width: calc(100% - 50px);
}
textarea{
	margin: 0 25px;
	border: none;
	outline: none;
	border-bottom: 1px solid #42B983;
	width: calc(100% - 50px);
	min-height: 120px;
}
.add_btn{
	margin: auto;
	margin-top: 25px;
	width: 30%;
	height: 40px;
	line-height: 40px;
	background: #42B983;
	color: #fff;
	text-align: center;
	border-radius: 5px;
	font-size: 15px;
}
.cancel{
	background: #fff;
	color: #42B983;
	border: 1px solid #42B983;
}
</style>
